.loading {
  width: 100px;
  margin: 0 auto;
  padding: 0;
  text-align: center; }
  .loading li {
    display: inline-block;
    width: 15px;
    height: 15px;
    margin: 5px;
    border-radius: 100%;
    background-color: black; }

.fade-each li {
  opacity: 0;
  -webkit-animation: fadeeach 0.6s infinite cubic-bezier(0.25, 0.25, 0.75, 0.75); }
  .fade-each li:nth-child(1) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s; }
  .fade-each li:nth-child(2) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s; }
  .fade-each li:nth-child(3) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s; }

@-webkit-keyframes fadeeach {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }



